body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #333;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  border: 2px solid #ddd;
  border-radius: 5px;
  outline: none;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

button[type="submit"], .load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button[type="submit"]:hover, .load-more:hover {
  background-color: #0056b3;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  transition: transform .3s;
  display: block;
}

.photo-card:hover img {
  transform: scale(1.1);
}

.info {
  color: #fff;
  text-align: center;
  background-color: rgba(0, 0, 0, .8);
  padding: 10px;
}

.info p {
  margin: 5px 0;
  font-size: 14px;
}

.info-item b {
  color: #ffc107;
}

